@mentionPrefixCls: rc-editor-mention;

.@{mentionPrefixCls} {
  &-wrapper {
    line-height: 26px;
    font-size: 12px;
    height: 26px;
    border: 1px solid #D9D9D9;
    border-radius: 5px;
  }
  &-editor {
    width: 100%;
    text-align: center;
    outline: 0;
    -moz-appearance: textfield;
    line-height: 26px;
    height: 26px;
    transition: all 0.3s ease;
    color: #666666;
    border: 0;
    border-radius: 5px;
    padding: 0;
  }
  &-dropdown {
    position: absolute;
    background-color: white;
    padding: 5px;
    max-height: 200px;
    overflow-y: auto;
    line-height: 1;

    &-item {
      &.focus {
        background-color: #e6f3ff;
      }
    }
  }
}

.avatar {
  float: left;
  width: 32px;
  height: 32px;
  margin-right: 5px;
  display: inline-block;
  overflow: hidden;
  line-height: 1;
  vertical-align: middle;
  border-radius: 3px;
}

.tag-avatar {
  width: 16px;
  height: 16px;
}


